import {Component} from "react";
import '../styles/home.css'
import Post from "./PostComponent";
import * as NasaAPI from '../utils/NasaAPI'
class Home extends Component{
    state = {
        imgs: []
    }
    componentDidMount(){
        NasaAPI.getApod()
        .then(res => {
            const data = res.map((item) => {
                const post = {
                    url: item.url,
                    date: item.date,
                    desc: item.explanation
                }
                return post
            })
            return data
        })
        .then(data => this.setState({
            imgs: data
        }))
        
    }
    render(){
        const array = this.state.imgs
        return(
            <div>
                {array.map((nuum) => {
                    return (
                        <Post 
                            data = {nuum}
                        />
                    )
                })   
                }
          </div>
        )
    }
}

export default Home