import react,{Component} from "react";

class Post extends Component{
    render(){
        const { data } = this.props
        return(
            <div className="post">
                <div className="post-header">
                    <a><h3 className="post-user">Astronomy Photo of the Day</h3></a>
                    <h5 className="post-date">{data.date}</h5>                    
                </div>
                <section >
                    <a href={data.url}><div className="post-image" style={{backgroundImage: `url(${data.url})`}}></div></a>
                </section>
                <section className="post-footer">
                    <h5 className="post-subtitle">{data.url}</h5>
                    <span className="post-description"><p>{data.desc}</p></span>
                </section>
            </div>            
        )
    }
}
export default Post